import helper from '../../common/helper.js'
export default {
	data() {
		return {
			PlanArray: '',
		};
	},
	onLoad() {
		this.Domaininfo();
		
	},
	onShow() {
		this.Domaininfo()
	},
	methods: {
		// 获取图文列表
		Domaininfo() {
			uni.showLoading({
					title: "加载中....",
					mask: true
				}),
				uni.request({
					url: helper.websiteUrl + 'help/getheples',
					method: 'GET',
					data: helper.postdata({}),
					success: res => {
						if (res.data.code == 200) {
							uni.hideLoading()
							this.PlanArray = res.data.data.baseinfo
						} else {
							helper.goout(res.data.info);
							uni.showToast({
								icon: 'none',
								title: res.data.info
							});
						}
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					}
				});
		},
		//进入图文详情页
		opendomain(e) {
			uni.navigateTo({
				url: 'helptext-content?bzid=' + e.currentTarget.dataset.bzid 
			});
		},
	}
}
